import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CommonListModal } from '../models/common-list-modal';

import * as moment from 'moment/moment';
import { LoginPage } from '../login/login';
import { CommonPosmodal } from '../models/common-posmodal';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { ServicesServiceProvider } from '../../providers/services-service/services-service';

@IonicPage()
@Component({
  selector: 'page-service-details',
  templateUrl: 'service-details.html',
})
export class ServiceDetailsPage {
  selectedService: CommonListModal;
  serviceDetails: CommonPosmodal = new CommonPosmodal();

  organizationString: string = "";
  createdByString: string = "";
  updatedByString: string = "";
  startEndDateString: string = "";

  companyString: string = "";
  stageString: string = "";
  typeString: string = "";
  sourceString: string = "";
  inchargeString: string = "";
  
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public authService: AuthenticationServiceProvider,
    public servicesServiceProvider: ServicesServiceProvider) {
    this.selectedService = navParams.get('selectedService');
    this.getCompanyDetailsByData(this.selectedService.id);
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad ServiceDetailsPage');
  }

  getCompanyDetailsByData(dataId) {
    console.log(dataId);
    // Show Loader
    this.authService.showLoaderFn('');
    // API Call
    this.servicesServiceProvider.getDetailsByData(dataId).subscribe(data => {

      if (data["statusCode"] === 0) {
        // Hide Loader
        this.authService.hideLoaderFn();
        this.serviceDetails = JSON.parse(data['response']) as CommonPosmodal;

        this.createdByString = '';
        if(this.serviceDetails.createdBy && this.serviceDetails.createdBy.fullName) {
          this.createdByString += this.serviceDetails.createdBy.fullName + ' on ';
          if(this.serviceDetails.createTimestamp) {
            this.createdByString +=  moment(this.serviceDetails.createTimestamp).format("DD MMM YYYY HH:mm");
          } 
        } else {
          this.createdByString = '----';
        }

        this.updatedByString = '';
        if(this.serviceDetails.updatedBy && this.serviceDetails.updatedBy.fullName) {
          this.updatedByString += this.serviceDetails.updatedBy.fullName + ' on ';
          if(this.serviceDetails.updateTimestamp) {
            this.updatedByString +=  moment(this.serviceDetails.updateTimestamp).format("DD MMM YYYY HH:mm");
          } 
        } else {
          this.updatedByString = '----';
        }

        if(this.serviceDetails.organization && this.serviceDetails.organization.organizationName) {
          this.organizationString = this.serviceDetails.organization.organizationName;
        } else {
          this.organizationString = '----';
        }  

        this.startEndDateString = "";
        if(this.serviceDetails.startDate) {
          this.startEndDateString +=  moment(this.serviceDetails.startDate).format("DD MMM YYYY");
        } 

        if(this.serviceDetails.endDate) {
          this.startEndDateString +=  ' - '
          + moment(this.serviceDetails.endDate).format("DD MMM YYYY");;
        } 
        
        if(this.serviceDetails.company && this.serviceDetails.company.companyName) {
          this.companyString = this.serviceDetails.company.companyName;
        } else {
          this.companyString = "----";
        }

        if(this.serviceDetails.stage && this.serviceDetails.stage.name) {
          this.stageString = this.serviceDetails.stage.name;
        } else {
          this.stageString = "----";
        }

        if(this.serviceDetails.stage && this.serviceDetails.stage.name) {
          this.stageString = this.serviceDetails.stage.name;
        } else {
          this.stageString = "----";
        }

        if(this.serviceDetails.type && this.serviceDetails.type.name) {
          this.typeString = this.serviceDetails.type.name;
        } else {
          this.typeString = "----";
        }

        if(this.serviceDetails.source && this.serviceDetails.source.name) {
          this.sourceString = this.serviceDetails.source.name;
        } else {
          this.sourceString = "----";
        }

        if(this.serviceDetails.projectIncharge && this.serviceDetails.projectIncharge.name) {
          this.inchargeString = this.serviceDetails.projectIncharge.name;
        } else {
          this.inchargeString = "----";
        }

      } else {
        this.authService.handleResponseStatusFn(data);
        if (data["statusCode"] === 2) {
          this.navCtrl.setRoot(LoginPage);
        }
      }  
    }, err => {
      // Hide Loader and Show Message
      this.authService.handleErrorFn(err);
      if (err.status === 401) {
        this.navCtrl.setRoot(LoginPage);
      }
    });
  }

}



