import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { NavController, NavParams, LoadingController, ToastController } from 'ionic-angular';

import { environment } from '../../environments/environment';
import { LoginPage } from '../../pages/login/login';

@Injectable()
export class AuthenticationServiceProvider {

  loadingDefaultMessage: string = 'Please wait...';
  successDefaultMessage: string = 'Operation Success.';
  sessionExpiredMessage: string = 'Session Expired, login in again.';
  errorDefaultMessage: string = 'Some error occured, try again.';
  loadingObj;

  private token: string;
  private username: string;
  private userId: number;

  private static readonly SIGNIN_URL = environment.apiUrl + '/security/loginByGet/';
  private static readonly TOKEN_VALID_URL = environment.apiUrl + '/security/account/';
  private static readonly TOKEN_CHECK_URL = environment.apiUrl + '/security/token/checkSession/';
  private static readonly REFRESH_TOKEN_URL = environment.apiUrl + '/security/token/refresh/';

  constructor(public http: HttpClient, 
    public loadingCtrl: LoadingController,
    private toastCtrl: ToastController) {

  }

  checkUserSessionService() {
    const currentUser = localStorage.getItem('misMobUser');
    if (currentUser != null) {
      const currentUserJson = JSON.parse(currentUser);
      if (currentUserJson.userToken) {
        return true;
      } else {
        localStorage.removeItem('misMobUser');
        return false;
      }
    } else {
      return false;
    }
  }

  getUserToken() {
    const currentUser = localStorage.getItem('misMobUser');
    if (currentUser != null) {
      const currentUserJson = JSON.parse(currentUser);

      if (currentUserJson.userToken) {
        return currentUserJson.userToken;
      } else {
        // remove user from local storage to log user out
        localStorage.removeItem('misMobUser');
        return '';
      }
    } else {
      return '';
    }
  }

  getUserDetails() {
    const currentUser = localStorage.getItem('misMobUser');
    if (currentUser != null) {
      const currentUserJson = JSON.parse(currentUser);
      if (currentUserJson.userToken) {
        return currentUserJson;
      } else {
        localStorage.removeItem('misMobUser');
        return null;
      }
    } else {
      return null;
    }
  }

  logoutService() {
    localStorage.removeItem('misMobUser');
  }

  	/**
	  * Check token for given user
	  * @param username
	  * @param token
	 */
  public checkTokenValidity() {
    const userToken = this.getUserToken();
    let urlTemp = AuthenticationServiceProvider.TOKEN_CHECK_URL  + userToken + ".do";
    return this.http.get(urlTemp)
    .map(res => <any>res);
  }

  /**
	  * Check token for given user
	  * @param username
	  * @param token
	 */
  public checkTokenData() {
    const userToken = this.getUserToken();
    let urlTemp = AuthenticationServiceProvider.TOKEN_CHECK_URL  + userToken + ".do";
    return this.http.get(urlTemp)
    .map(res => <any>res);
      // .map((response: Response) => {
      //   let userSession = response.json();
      //   localStorage.setItem('misMobUser', JSON.stringify(userSession));
      // });
  } 

  /**
	  * Fetches and saves token for given user
	  * @param username
	  * @param password
	  */
   public signIn(username: string, password: string) {
    let urlTemp = AuthenticationServiceProvider.SIGNIN_URL  + username + '/' + password + ".do";
    return this.http.get(urlTemp)
    .map(res => <any>res);
  }

  showLoaderFn(msg){
    this.loadingObj = this.loadingCtrl.create({content : this.loadingDefaultMessage});
    if(msg !== '') {
      this.loadingObj.setContent(msg);
    }
    
    this.loadingObj.present();
  }

  hideLoaderFn(){
    this.loadingObj.dismiss();
    return false;
  }

  showMessageToastFn(msg, cssClassVar){
    if(msg === '') {
      msg = this.successDefaultMessage;
    }

    // cssClassVar: 'warning', success, error

    let toast = this.toastCtrl.create({
      message: msg,
      position: 'bottom',
      cssClass: cssClassVar,
      duration: 3000,
      showCloseButton : true
    });

    toast.present();
  }

  showErrorMessageToastFn(msg){
    if(msg === '') {
      msg = this.errorDefaultMessage;
    }

    let toast = this.toastCtrl.create({
      message: msg,
      position: 'bottom',
      cssClass: 'error',
      showCloseButton : true
    });

    toast.present();
  }

  handleErrorFn(err){
    // Hide Loader
    this.hideLoaderFn();

    if(err.status === 401) {
      this.logoutService();
      this.showMessageToastFn(this.sessionExpiredMessage, 'warning');
    } else {
      this.showMessageToastFn(this.errorDefaultMessage, 'error');
    }
  }

  handleResponseStatusFn(data){
    if(data["statusCode"] === 2) {
      // Hide Loader
      this.hideLoaderFn();

      let msgData = data["msg"];
      if(msgData === '') {
        msgData = this.errorDefaultMessage;
      }
      this.logoutService();
      this.showMessageToastFn(msgData, 'warning');

    } else if(data["statusCode"] === 0 || data["statusCode"] !== 0) {
      // Hide Loader
      this.hideLoaderFn();

      let msgData = data["msg"];
      if(msgData === '') {
        msgData = this.errorDefaultMessage;
      }
      this.showMessageToastFn(msgData, 'error');
    }
    
  }

}



