import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { TasksServiceProvider } from '../../providers/tasks-service/tasks-service';
import { TaskDetailsForm } from '../models/task-details-form';
import { TaskDetailsPage } from '../task-details/task-details';
import * as moment from 'moment/moment';
import { TaskDetailsSaveUpdatePage } from '../task-details-save-update/task-details-save-update';
import { LoginPage } from '../login/login';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';

@IonicPage()
@Component({
  selector: 'page-tasks',
  templateUrl: 'tasks.html',
})
export class TasksPage {
  selectedItem: any;
  public selectedTaskDate: any;
  public selectedTaskDateDDMMMYYYY : any;
  // public taskDetailsFormList: any;
  public taskDetailsFormList: any[] = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public tasksServiceProvider: TasksServiceProvider,
    public authService: AuthenticationServiceProvider) {
    this.selectedTaskDate = new Date().toISOString();
    this.selectedTaskDateDDMMMYYYY = moment(this.selectedTaskDate).format("DD MMM YYYY");
    this.changeTaskDate(this.selectedTaskDate);
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad TasksPage');
  }

  getAllTasks(){
    
  }
  
  openTaskDetails(item) {
    this.navCtrl.push(TaskDetailsPage, {
      item: item
    });
  }

  getAllTaskByDate(selectedTaskDate) {
    // Show Loader
    // this.authService.showLoaderFn('');
    
    this.taskDetailsFormList = [];

    this.tasksServiceProvider.getAllTasksByData(0, 0, 'date', selectedTaskDate, 'no').subscribe(data => {
      if(data["statusCode"] === 0) {
        this.taskDetailsFormList = data['response'];// JSON.parse(data['response']);

        
      } else {
        this.authService.handleResponseStatusFn(data);
        if(data["statusCode"] === 2) {
          this.navCtrl.setRoot(LoginPage);
        }
      }

      // Hide Loader
      // this.authService.hideLoaderFn();
    },
    err => {
      // Hide Loader and Show Message
      this.authService.handleErrorFn(err);
      if(err.status === 401) {
        this.navCtrl.setRoot(LoginPage);
      }
    });
  }

  changeTaskDate(selectedTaskDate) {
    this.selectedTaskDate = moment(this.selectedTaskDate).format("YYYY-MM-DD");
    this.selectedTaskDateDDMMMYYYY = moment(this.selectedTaskDate).format("DD MMM YYYY");
    this.getAllTaskByDate(this.selectedTaskDate);
  }

  getAllTaskForNextDate() {
    let dateData = moment(this.selectedTaskDate).add(1, 'days');
    this.selectedTaskDate = dateData.format("YYYY-MM-DD");
    this.selectedTaskDateDDMMMYYYY = moment(this.selectedTaskDate).format("DD MMM YYYY");
    this.getAllTaskByDate(this.selectedTaskDate);
  }

  getAllTaskForPrevDate() {
    let dateData = moment(this.selectedTaskDate).subtract(1, 'days');
    this.selectedTaskDate = dateData.format("YYYY-MM-DD");
    this.selectedTaskDateDDMMMYYYY = moment(this.selectedTaskDate).format("DD MMM YYYY");
    this.getAllTaskByDate(this.selectedTaskDate);
  }

  openTaskSaveUpdateDetails(item) {
    this.navCtrl.push(TaskDetailsSaveUpdatePage, {
      item: item
    });
  }

}



