import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { ContactsServiceProvider } from '../../providers/contacts-service/contacts-service';
import { ContactUIForm } from '../models/contact-ui-form';
import { CompaniesPage } from '../companies/companies';
import { LoginPage } from '../login/login';
import { Company } from '../models/company';
import { CompaniesServiceProvider } from '../../providers/companies-service/companies-service';
import { CommonModel } from '../models/common-model';

@IonicPage()
@Component({
  selector: 'page-company-save-update',
  templateUrl: 'company-save-update.html',
})
export class CompanySaveUpdatePage {
  selectedCompany: Company;

  submitAttempt: boolean = false;

  companyObjFormGroup: FormGroup;
  companyObj: Company= new Company();

  public countriesList: CommonModel[] = [];

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public formBuilder: FormBuilder, 
    public authService: AuthenticationServiceProvider,
    public companiesServiceProvider: CompaniesServiceProvider,) {

    let countryTemp: CommonModel = new CommonModel();
    countryTemp.id = 1;
    countryTemp.name = 'India';
    this.countriesList[0] = countryTemp;

    this.selectedCompany = navParams.get('selectedCompany');

    if(this.selectedCompany) {
      this.selectedCompany = this.selectedCompany;
    }

    this.saveUpdateFormValidate();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CompanySaveUpdatePage');
  }

  saveUpdateCompany() {
    this.submitAttempt = true;
    // Call for save

    console.log('validddddddd----' + this.companyObjFormGroup.valid);
    console.log('validddddddd----' + this.companyObj);
    console.log('validddddddd----' + JSON.stringify(this.companyObj));

    if (this.companyObjFormGroup.valid) {
      // Show Loader
      this.authService.showLoaderFn('');
      this.companiesServiceProvider.saveCompany(this.companyObj).subscribe(data => {

        if(data["statusCode"] === 0) {
          this.navCtrl.setRoot(CompaniesPage);
          // Hide Loader
          this.authService.hideLoaderFn();
          this.authService.showMessageToastFn('Contact saved successfully.', 'success');
        } else {
          this.authService.handleResponseStatusFn(data);
          if(data["statusCode"] === 2) {
            this.navCtrl.setRoot(LoginPage);
          }
        }
        
      },
      err => {
        // Hide Loader and Show Message
        this.authService.handleErrorFn(err);
        if(err.status === 401) {
          this.navCtrl.setRoot(LoginPage);
        }
      });

    }

  }  

   // "groupCompany": new FormControl(this.companyObj.groupCompany, [
      //   Validators.compose([Validators.required])
      // ]),

  saveUpdateFormValidate() {
    const emailPattern = "^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$";

    this.companyObjFormGroup = this.formBuilder.group({
      "companyName": new FormControl(this.companyObj.companyName, [
        Validators.compose([Validators.maxLength(100), 
        Validators.pattern('[a-zA-Z0-9 ]*'), Validators.required])
      ]),
      "website": new FormControl(this.companyObj.website, [
        Validators.compose([Validators.maxLength(100), 
        Validators.pattern('[a-zA-Z0-9. ]*')])
      ]),

      "addressLine1": new FormControl(this.companyObj.addressLine1, [
        Validators.compose([Validators.maxLength(100), 
        Validators.pattern('[a-zA-Z ]*')])
      ]),
      "addressLine2": new FormControl(this.companyObj.addressLine2, [
        Validators.compose([Validators.maxLength(100)])
      ]), 

      "country": new FormControl(this.companyObj.country, [
        Validators.compose([Validators.maxLength(100)])
      ]),

      "state": new FormControl(this.companyObj.state, [
        Validators.compose([Validators.maxLength(100)])
      ]),

      "city": new FormControl(this.companyObj.city, [
        Validators.compose([Validators.maxLength(100)])
      ]),

      "pincode": new FormControl(this.companyObj.pincode, [
        Validators.compose([Validators.maxLength(20), Validators.required])
      ]),

      "number1": new FormControl(this.companyObj.number1, [
        Validators.compose([Validators.minLength(10), Validators.maxLength(20), 
        Validators.pattern('[0-9 ]*'), Validators.required])
      ]), 

      "email1": new FormControl(this.companyObj.email1, [
        Validators.compose([Validators.maxLength(100), 
        Validators.pattern(emailPattern), Validators.required])
      ]),

      "number2": new FormControl(this.companyObj.number2, [
        Validators.compose([Validators.minLength(8), Validators.maxLength(100), 
        Validators.pattern('[0-9+ ]*')])
      ]),

      "number3": new FormControl(this.companyObj.number3, [
        Validators.compose([Validators.minLength(8), Validators.maxLength(100), 
        Validators.pattern('[0-9+ ]*')])
      ]),

      "additionalInfo": new FormControl(this.companyObj.additionalInfo, [
        Validators.compose([Validators.minLength(4), Validators.maxLength(100), 
        Validators.pattern('[a-zA-Z ]*')])
      ]),
        
    });

  }  

}



