import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CompaniesServiceProvider } from '../../providers/companies-service/companies-service';
import 'rxjs/add/operator/catch';
import { LoginPage } from '../login/login';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { CommonListModal } from '../models/common-list-modal';
import { CompanyDetailsPage } from '../company-details/company-details';
import { CompanySaveUpdatePage } from '../company-save-update/company-save-update';
import { Company } from '../models/company';


@IonicPage()
@Component({
  selector: 'page-companies',
  templateUrl: 'companies.html',
})
export class CompaniesPage {

  public companiesModelList: Company[] = [];
  public companiesModelSearchList: Company[] = [];

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public companiesServiceProvider: CompaniesServiceProvider,
    public authService: AuthenticationServiceProvider) {
      this.getAllCompanies();

  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad CompaniesPage');
  }

  initializeItems() {
    this.companiesModelSearchList = [];
    this.companiesModelList.forEach(element => {
      this.companiesModelSearchList.push(element);
    });
  }

  getItems(ev: any) {
    // Reset items back to all of the items
    this.initializeItems();

    // set val to the value of the searchbar
    let val = ev.target.value;

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.companiesModelSearchList = this.companiesModelSearchList.filter((item) => {
        return (item.companyName.toLowerCase().indexOf(val.toLowerCase()) > -1);
      });
    }
  }

  getAllCompanies() {
    // Show Loader
    this.authService.showLoaderFn('');
    // API Call
    this.companiesServiceProvider.getAll().subscribe(data => {

      if (data["statusCode"] === 0) {
        this.companiesModelList = JSON.parse(data['response']);
        this.initializeItems();

        // Hide Loader
        this.authService.hideLoaderFn();
      } else {
        this.authService.handleResponseStatusFn(data);
        if (data["statusCode"] === 2) {
          this.navCtrl.setRoot(LoginPage);
        }
      }

    },
      err => {
        // Hide Loader and Show Message
        this.authService.handleErrorFn(err);
        if (err.status === 401) {
          this.navCtrl.setRoot(LoginPage);
        }
      });
  }

  viewCompanyDetails(selectedCompany) {
    this.navCtrl.push(CompanyDetailsPage, {
      selectedCompany: selectedCompany
    });
  }

  saveUpdateCompany(selectedCompany) {
    this.navCtrl.push(CompanySaveUpdatePage, {
      selectedCompany: selectedCompany
    });
  }

}



