import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { OrdersServiceProvider } from '../../providers/orders-service/orders-service';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { LoginPage } from '../login/login';
import { CommonListModal } from '../models/common-list-modal';
import { OrderDetailsPage } from '../order-details/order-details';

@IonicPage()
@Component({
  selector: 'page-orders',
  templateUrl: 'orders.html',
})
export class OrdersPage {

  public ordersList: CommonListModal[] = [];
  public ordersSearchList: CommonListModal[] = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public ordersServiceProvider: OrdersServiceProvider,
    public authService: AuthenticationServiceProvider) {

      // Show Loader
      this.authService.showLoaderFn('');
      // API Call
      this.ordersServiceProvider.getAll().subscribe(data => {

        if(data["statusCode"] === 0) {
          this.ordersList = JSON.parse(data['response']);
          this.initializeItems();
  
          // Hide Loader
          this.authService.hideLoaderFn();
        } else {
          this.authService.handleResponseStatusFn(data);
          if(data["statusCode"] === 2) {
            this.navCtrl.setRoot(LoginPage);
          }
        }
        
      },
      err => {
        // Hide Loader and Show Message
        this.authService.handleErrorFn(err);
        if(err.status === 401) {
          this.navCtrl.setRoot(LoginPage);
        }
      });
  }


  ionViewDidLoad() {
    // console.log('ionViewDidLoad OrdersPage');
  }

  initializeItems() {
    this.ordersSearchList = [];
    this.ordersList.forEach(element => {
      this.ordersSearchList.push(element);
    });
  }

  getItems(ev: any) {
    // Reset items back to all of the items
    this.initializeItems();

    // set val to the value of the searchbar
    let val = ev.target.value;

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.ordersSearchList = this.ordersList.filter((item) => {
        return (item.name.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
  }

  viewOrderDetails(selectedOrder) {
    this.navCtrl.push(OrderDetailsPage, {
      selectedOrder: selectedOrder
    });
  }

}



