import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CommonListModal } from '../models/common-list-modal';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { OrdersServiceProvider } from '../../providers/orders-service/orders-service';
import { CommonPosmodal } from '../models/common-posmodal';

import * as moment from 'moment/moment';
import { LoginPage } from '../login/login';

@IonicPage()
@Component({
  selector: 'page-order-details',
  templateUrl: 'order-details.html',
})
export class OrderDetailsPage {
  selectedOrder: CommonListModal;
  orderDetails: CommonPosmodal = new CommonPosmodal();

  organizationString: string = "";
  createdByString: string = "";
  updatedByString: string = "";
  startEndDateString: string = "";

  companyString: string = "";
  stageString: string = "";
  typeString: string = "";
  sourceString: string = "";
  inchargeString: string = "";
  
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public authService: AuthenticationServiceProvider,
    public ordersServiceProvider: OrdersServiceProvider) {
    this.selectedOrder = navParams.get('selectedOrder');
    this.getCompanyDetailsByData(this.selectedOrder.id);
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad OrderDetailsPage');
  }

  getCompanyDetailsByData(dataId) {
    console.log(dataId);
    // Show Loader
    this.authService.showLoaderFn('');
    // API Call
    this.ordersServiceProvider.getDetailsByData(dataId).subscribe(data => {

      if (data["statusCode"] === 0) {
        // Hide Loader
        this.authService.hideLoaderFn();
        this.orderDetails = JSON.parse(data['response']) as CommonPosmodal;

        this.createdByString = '';
        if(this.orderDetails.createdBy && this.orderDetails.createdBy.fullName) {
          this.createdByString += this.orderDetails.createdBy.fullName + ' on ';
          if(this.orderDetails.createTimestamp) {
            this.createdByString +=  moment(this.orderDetails.createTimestamp).format("DD MMM YYYY HH:mm");
          } 
        } else {
          this.createdByString = '----';
        }

        this.updatedByString = '';
        if(this.orderDetails.updatedBy && this.orderDetails.updatedBy.fullName) {
          this.updatedByString += this.orderDetails.updatedBy.fullName + ' on ';
          if(this.orderDetails.updateTimestamp) {
            this.updatedByString +=  moment(this.orderDetails.updateTimestamp).format("DD MMM YYYY HH:mm");
          } 
        } else {
          this.updatedByString = '----';
        }

        if(this.orderDetails.organization && this.orderDetails.organization.organizationName) {
          this.organizationString = this.orderDetails.organization.organizationName;
        } else {
          this.organizationString = '----';
        }  

        this.startEndDateString = "";
        if(this.orderDetails.startDate) {
          this.startEndDateString +=  moment(this.orderDetails.startDate).format("DD MMM YYYY");
        } 

        if(this.orderDetails.endDate) {
          this.startEndDateString +=  ' - '
          + moment(this.orderDetails.endDate).format("DD MMM YYYY");;
        } 
        
        if(this.orderDetails.company && this.orderDetails.company.companyName) {
          this.companyString = this.orderDetails.company.companyName;
        } else {
          this.companyString = "----";
        }

        if(this.orderDetails.stage && this.orderDetails.stage.name) {
          this.stageString = this.orderDetails.stage.name;
        } else {
          this.stageString = "----";
        }

        if(this.orderDetails.stage && this.orderDetails.stage.name) {
          this.stageString = this.orderDetails.stage.name;
        } else {
          this.stageString = "----";
        }

        if(this.orderDetails.type && this.orderDetails.type.name) {
          this.typeString = this.orderDetails.type.name;
        } else {
          this.typeString = "----";
        }

        if(this.orderDetails.source && this.orderDetails.source.name) {
          this.sourceString = this.orderDetails.source.name;
        } else {
          this.sourceString = "----";
        }

        if(this.orderDetails.projectIncharge && this.orderDetails.projectIncharge.name) {
          this.inchargeString = this.orderDetails.projectIncharge.name;
        } else {
          this.inchargeString = "----";
        }

      } else {
        this.authService.handleResponseStatusFn(data);
        if (data["statusCode"] === 2) {
          this.navCtrl.setRoot(LoginPage);
        }
      }  
    }, err => {
      // Hide Loader and Show Message
      this.authService.handleErrorFn(err);
      if (err.status === 401) {
        this.navCtrl.setRoot(LoginPage);
      }
    });
  }

}



