import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, ToastController } from 'ionic-angular';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';


import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { ContactsPage } from '../contacts/contacts';
import { CompaniesPage } from '../companies/companies';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  loginModel: any = {};
  loginFormGroup: FormGroup;
  submitAttempt: boolean = false;
  loadingDefaultMessage: string = 'Authenticating, please wait...';
  successDefaultMessage: string = 'Authenticated Successfully';

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams, 
    public authService: AuthenticationServiceProvider, 
    private formBuilder: FormBuilder) {
    this.createLoginForm();

    // Show Loader
    this.authService.showLoaderFn(this.loadingDefaultMessage);

    let checkUserSessionFlag = this.authService.checkUserSessionService();
    if (checkUserSessionFlag === true) {

      // Check token validity
      this.authService.checkTokenValidity().subscribe(data => {
        // Hide Loader
        this.authService.hideLoaderFn();
        // Show success message
        this.authService.showMessageToastFn(this.successDefaultMessage, 'success');
        // Open Homepage
        this.openHomePage();
        },
        err => {
          // Hide Loader and Show Message
          this.authService.handleErrorFn(err);
        });

    } else {
      // Hide Loader
      this.authService.hideLoaderFn();
    }
  }

  ionViewDidLoad() {

  }

  createLoginForm() {
    this.loginFormGroup = this.formBuilder.group({
      username: ['', [Validators.required, Validators.email, Validators.minLength(5), Validators.maxLength(150)]],
      password: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(300)]],
    });
  }

  login() {
    this.submitAttempt = true;
    if (this.loginFormGroup.valid) {
      // Show Loader
      this.authService.showLoaderFn(this.loadingDefaultMessage);

      // this.loadingObj.present().then(()=>{
        this.authService.signIn(this.loginModel.username, this.loginModel.password)
          .subscribe(
            data => {
              localStorage.setItem('misMobUser', JSON.stringify(data));
              this.authService.showMessageToastFn(this.successDefaultMessage, 'success');
              // Open Homepage
              this.openHomePage();
              // Hide Loader
              this.authService.hideLoaderFn();
              
            },
            error => {

              // Hide Loader
              this.authService.hideLoaderFn();
              this.authService.showErrorMessageToastFn('');

              let cusErroJson = JSON.parse(error['_body']);
              let customizedErrorMsg = cusErroJson.message + ': ' + cusErroJson.error.message;
              if (error.status === 401) {

              }

            });
          // });   
    } else {

    }
  }

  openHomePage(){
    this.navCtrl.setRoot(CompaniesPage);// Main
    // this.navCtrl.setRoot(ContactsPage);
  }

  openLoginPage(){
    this.navCtrl.setRoot(LoginPage);
  }

}



