import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { ContactUIForm } from '../models/contact-ui-form';
import { ContactsServiceProvider } from '../../providers/contacts-service/contacts-service';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { LoginPage } from '../login/login';
import { ContactsPage } from '../contacts/contacts';
import { CompaniesServiceProvider } from '../../providers/companies-service/companies-service';
import { Company } from '../models/company';
import { CommonListModal } from '../models/common-list-modal';


@IonicPage()
@Component({
  selector: 'page-contact-full-details',
  templateUrl: 'contact-full-details.html',
})
export class ContactFullDetailsPage {
  selectedContact: ContactUIForm;
  submitAttempt: boolean = false;

  contactsForm: FormGroup;
  contactUIForm: ContactUIForm= new ContactUIForm();
  
  public companiesModelList: Company[] = [];
  public designationsList: CommonListModal[] = [];

  companyNameString: string = "";

  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public formBuilder: FormBuilder, 
    public contactsServiceProvider: ContactsServiceProvider,
    public authService: AuthenticationServiceProvider,
    public companiesServiceProvider: CompaniesServiceProvider,) {

    this.selectedContact = navParams.get('selectedContact');
    if(this.selectedContact) {
      this.contactUIForm = this.selectedContact;
    }

    this.saveContactFormValidate();
    this.getAllCompanies();
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad ContactFullDetailsPage');
  }

  getAllCompanies() {
    // Show Loader
    this.authService.showLoaderFn('');
    // API Call
    this.companiesServiceProvider.getAll().subscribe(data => {

      if (data["statusCode"] === 0) {
        this.companiesModelList = JSON.parse(data['response']);

        // Hide Loader
        this.authService.hideLoaderFn();

        this.getAllDesignations();
      } else {
        this.authService.handleResponseStatusFn(data);
        if (data["statusCode"] === 2) {
          this.navCtrl.setRoot(LoginPage);
        }
      }

    },
      err => {
        // Hide Loader and Show Message
        this.authService.handleErrorFn(err);
        if (err.status === 401) {
          this.navCtrl.setRoot(LoginPage);
        }
      });
  }

  getAllDesignations() {
    // Show Loader
    this.authService.showLoaderFn('');
    // API Call
    this.contactsServiceProvider.getAllDesignationsAPI().subscribe(data => {

      if (data["statusCode"] === 0) {
        this.designationsList = JSON.parse(data['response']);

        // Hide Loader
        this.authService.hideLoaderFn();

        if(this.selectedContact.contactId) {
          this.getContactDetailsByData(this.selectedContact.contactId);
        }
        
      } else {
        this.authService.handleResponseStatusFn(data);
        if (data["statusCode"] === 2) {
          this.navCtrl.setRoot(LoginPage);
        }
      }

    },
      err => {
        // Hide Loader and Show Message
        this.authService.handleErrorFn(err);
        if (err.status === 401) {
          this.navCtrl.setRoot(LoginPage);
        }
      });
  }

  getContactDetailsByData(dataId) {
    // Show Loader
    this.authService.showLoaderFn('');
    // API Call
    this.contactsServiceProvider.getContactDetailsByData(dataId).subscribe(data => {

      if (data["statusCode"] === 0) {
        // Hide Loader
        this.authService.hideLoaderFn();
        this.contactUIForm = JSON.parse(data['response']) as ContactUIForm;

        this.companyNameString = this.contactUIForm.company.companyName;
      } else {
        this.authService.handleResponseStatusFn(data);
        if (data["statusCode"] === 2) {
          this.navCtrl.setRoot(LoginPage);
        }
      }
    }, err => {
      // Hide Loader and Show Message
      this.authService.handleErrorFn(err);
      if (err.status === 401) {
        this.navCtrl.setRoot(LoginPage);
      }
    });
  }

  saveContactForm() {
    this.submitAttempt = true;
    // Call for save

    // console.log('validddddddd----' + this.contactsForm.valid);
    // console.log('validddddddd----' + this.contactUIForm);
    if (this.contactsForm.valid) {
      // Show Loader
      this.authService.showLoaderFn('');
      
      
      this.contactsServiceProvider.saveContact(this.contactUIForm).subscribe(data => {

        if(data["statusCode"] === 0) {
          this.navCtrl.setRoot(ContactsPage);
          // Hide Loader
          this.authService.hideLoaderFn();
          this.authService.showMessageToastFn('Contact saved successfully.', 'success');
        } else {
          this.authService.handleResponseStatusFn(data);
          if(data["statusCode"] === 2) {
            this.navCtrl.setRoot(LoginPage);
          }
        }
        
      },
      err => {
        // Hide Loader and Show Message
        this.authService.handleErrorFn(err);
        if(err.status === 401) {
          this.navCtrl.setRoot(LoginPage);
        }
      });

    }
  }  

  saveContactFormValidate() {
    const emailPattern = "^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$";

    this.contactsForm = this.formBuilder.group({
      "contactId": new FormControl(this.contactUIForm.contactId, []),

      "permissionType": new FormControl(this.contactUIForm.contactName, [
        Validators.compose([Validators.required])
      ]),

      "prefix": new FormControl(this.contactUIForm.contactName, [
        Validators.compose([Validators.required])
      ]),
      
      "contactName": new FormControl(this.contactUIForm.contactName, [
        Validators.compose([Validators.maxLength(100), 
        Validators.pattern('[a-zA-Z ]*'), Validators.required])
      ]),

      "company": new FormControl(this.contactUIForm.company, [
        Validators.compose([Validators.required])
      ]),

      "designation": new FormControl(this.contactUIForm.designation, [
        Validators.compose([Validators.required])
      ]),

      "number1": new FormControl(this.contactUIForm.number1, [
        Validators.compose([Validators.minLength(10), Validators.maxLength(20), 
        Validators.pattern('[0-9 ]*'), Validators.required])
      ]), 

      "email1": new FormControl(this.contactUIForm.email1, [
        Validators.compose([Validators.maxLength(100), 
        Validators.pattern(emailPattern), Validators.required])
      ]),

      "number2": new FormControl(this.contactUIForm.number2, [
        Validators.compose([Validators.minLength(8), Validators.maxLength(100), 
        Validators.pattern('[0-9+ ]*')])
      ]),

      "number3": new FormControl(this.contactUIForm.number3, [
        Validators.compose([Validators.minLength(8), Validators.maxLength(100), 
        Validators.pattern('[0-9+ ]*')])
      ]),

      "contactNote": new FormControl(this.contactUIForm.contactNote, [
        Validators.compose([Validators.minLength(4), Validators.maxLength(100), 
        Validators.pattern('[a-zA-Z ]*')])
      ]),
        
    });

  }  


}



