import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CommonListModal } from '../models/common-list-modal';
import { ProjectsServiceProvider } from '../../providers/projects-service/projects-service';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { CommonPosmodal } from '../models/common-posmodal';

import * as moment from 'moment/moment';
import { LoginPage } from '../login/login';

@IonicPage()
@Component({
  selector: 'page-project-details',
  templateUrl: 'project-details.html',
})
export class ProjectDetailsPage {
  selectedProject: CommonListModal;
  projectDetails: CommonPosmodal = new CommonPosmodal();

  organizationString: string = "";
  createdByString: string = "";
  updatedByString: string = "";
  startEndDateString: string = "";

  companyString: string = "";
  stageString: string = "";
  typeString: string = "";
  sourceString: string = "";
  inchargeString: string = "";
  
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public authService: AuthenticationServiceProvider,
    public projectsServiceProvider: ProjectsServiceProvider) {
    this.selectedProject = navParams.get('selectedProject');
    this.getCompanyDetailsByData(this.selectedProject.id);
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad ProjectDetailsPage');
  }

  getCompanyDetailsByData(dataId) {
    console.log(dataId);
    // Show Loader
    this.authService.showLoaderFn('');
    // API Call
    this.projectsServiceProvider.getDetailsByData(dataId).subscribe(data => {

      if (data["statusCode"] === 0) {
        // Hide Loader
        this.authService.hideLoaderFn();
        this.projectDetails = JSON.parse(data['response']) as CommonPosmodal;

        this.createdByString = '';
        if(this.projectDetails.createdBy && this.projectDetails.createdBy.fullName) {
          this.createdByString += this.projectDetails.createdBy.fullName + ' on ';
          if(this.projectDetails.createTimestamp) {
            this.createdByString +=  moment(this.projectDetails.createTimestamp).format("DD MMM YYYY HH:mm");
          } 
        } else {
          this.createdByString = '----';
        }

        this.updatedByString = '';
        if(this.projectDetails.updatedBy && this.projectDetails.updatedBy.fullName) {
          this.updatedByString += this.projectDetails.updatedBy.fullName + ' on ';
          if(this.projectDetails.updateTimestamp) {
            this.updatedByString +=  moment(this.projectDetails.updateTimestamp).format("DD MMM YYYY HH:mm");
          } 
        } else {
          this.updatedByString = '----';
        }

        if(this.projectDetails.organization && this.projectDetails.organization.organizationName) {
          this.organizationString = this.projectDetails.organization.organizationName;
        } else {
          this.organizationString = '----';
        }  

        this.startEndDateString = "";
        if(this.projectDetails.startDate) {
          this.startEndDateString +=  moment(this.projectDetails.startDate).format("DD MMM YYYY");
        } 

        if(this.projectDetails.endDate) {
          this.startEndDateString +=  ' - '
          + moment(this.projectDetails.endDate).format("DD MMM YYYY");
        } 
        
        if(this.projectDetails.company && this.projectDetails.company.companyName) {
          this.companyString = this.projectDetails.company.companyName;
        } else {
          this.companyString = "----";
        }

        if(this.projectDetails.stage && this.projectDetails.stage.name) {
          this.stageString = this.projectDetails.stage.name;
        } else {
          this.stageString = "----";
        }

        if(this.projectDetails.stage && this.projectDetails.stage.name) {
          this.stageString = this.projectDetails.stage.name;
        } else {
          this.stageString = "----";
        }

        if(this.projectDetails.type && this.projectDetails.type.name) {
          this.typeString = this.projectDetails.type.name;
        } else {
          this.typeString = "----";
        }

        if(this.projectDetails.source && this.projectDetails.source.name) {
          this.sourceString = this.projectDetails.source.name;
        } else {
          this.sourceString = "----";
        }

        if(this.projectDetails.projectIncharge && this.projectDetails.projectIncharge.name) {
          this.inchargeString = this.projectDetails.projectIncharge.name;
        } else {
          this.inchargeString = "----";
        }

      } else {
        this.authService.handleResponseStatusFn(data);
        if (data["statusCode"] === 2) {
          this.navCtrl.setRoot(LoginPage);
        }
      }  
    }, err => {
      // Hide Loader and Show Message
      this.authService.handleErrorFn(err);
      if (err.status === 401) {
        this.navCtrl.setRoot(LoginPage);
      }
    });
  }

}



