import { Component, ViewChild } from '@angular/core';
import { Nav, Platform, AlertController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomePage } from '../pages/home/home';

import { TasksPage } from '../pages/tasks/tasks';
import { ContactsPage } from '../pages/contacts/contacts';
import { CompaniesPage } from '../pages/companies/companies';
import { ProjectsPage } from '../pages/projects/projects';
import { OrdersPage } from '../pages/orders/orders';
import { ServicesPage } from '../pages/services/services';
import { LoginPage } from '../pages/login/login';
import { NotificationsPage } from '../pages/notifications/notifications';
import { AuthenticationServiceProvider } from '../providers/authentication-service/authentication-service';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = LoginPage;

  pages: Array<{title: string, component: any}>;

  userFullName: string = '';
  userEmail: string = '';
  userRoleName: string = '';
  userRoleId: number = 0;

  constructor(public platform: Platform, 
    public statusBar: StatusBar, 
    public splashScreen: SplashScreen,
    private alertCtrl: AlertController,
    public authService: AuthenticationServiceProvider) {
    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [
      // { title: 'Home', component: HomePage },
      { title: 'Companies', component: CompaniesPage },
      { title: 'Contacts', component: ContactsPage },
      { title: 'Tasks', component: TasksPage },
      { title: 'Projects', component: ProjectsPage },
      { title: 'Orders', component: OrdersPage },
      { title: 'Services', component: ServicesPage },
      { title: 'Notifications', component: NotificationsPage },
    ];

  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  menuOpened() {
    const currentUserJson = this.authService.getUserDetails();
    this.userFullName = currentUserJson.fullName;
    this.userEmail = currentUserJson.userId;
    this.userRoleName = currentUserJson.userRoleName;
    this.userRoleId = currentUserJson.userRoleId;
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }

  logoutMainFn() {
    this.logoutConfirm();
  }

  logoutConfirm() {
    let alert = this.alertCtrl.create({
      title: 'MISDesk CRM',
      message: 'Are you sure you want to logout?',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            // console.log('Cancel clicked');
          }
        },
        {
          text: 'Logout',
          handler: () => {
            // Logout User
            localStorage.removeItem('misMobUser');
            // Open Loginpage
            this.nav.setRoot(LoginPage);
            this.authService.showMessageToastFn('Logged out successfully.', 'success');
          }
        }
      ]
    });
    alert.present();
  }
}



