import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ServicesServiceProvider } from '../../providers/services-service/services-service';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { LoginPage } from '../login/login';
import { CommonListModal } from '../models/common-list-modal';
import { ServiceDetailsPage } from '../service-details/service-details';

@IonicPage()
@Component({
  selector: 'page-services',
  templateUrl: 'services.html',
})
export class ServicesPage {

  public servicesList: CommonListModal[] = [];
  public servicesSearchList: CommonListModal[] = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public servicesServiceProvider: ServicesServiceProvider,
    public authService: AuthenticationServiceProvider) {

    // Show Loader
    this.authService.showLoaderFn('');
    // API Call
    this.servicesServiceProvider.getAll().subscribe(data => {

      if(data["statusCode"] === 0) {
        this.servicesList = JSON.parse(data['response']);
        this.initializeItems();

        // Hide Loader
        this.authService.hideLoaderFn();
      } else {
        this.authService.handleResponseStatusFn(data);
        if(data["statusCode"] === 2) {
          this.navCtrl.setRoot(LoginPage);
        }
      }
      
    },
    err => {
      // Hide Loader and Show Message
      this.authService.handleErrorFn(err);
      if(err.status === 401) {
        this.navCtrl.setRoot(LoginPage);
      }
    });

  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad ServicesPage');
  }

  initializeItems() {
    this.servicesSearchList = [];
    this.servicesList.forEach(element => {
      this.servicesSearchList.push(element);
    });
  }

  getItems(ev: any) {
    // Reset items back to all of the items
    this.initializeItems();

    // set val to the value of the searchbar
    let val = ev.target.value;

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.servicesSearchList = this.servicesList.filter((item) => {
        return (item.name.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
  }

  viewServiceDetails(selectedService) {
    this.navCtrl.push(ServiceDetailsPage, {
      selectedService: selectedService
    });
  }

}



