import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';

@IonicPage()
@Component({
  selector: 'page-task-details',
  templateUrl: 'task-details.html',
})
export class TaskDetailsPage {
  public toggleSubtasksFlag:boolean = false;
  selectedItem: any;

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public authService: AuthenticationServiceProvider) {

    // Show Loader
    this.authService.showLoaderFn('');
    // API Call
    this.selectedItem = navParams.get('item');
    
    // Hide Loader
    this.authService.hideLoaderFn();
  }

  ionViewDidLoad() {
  }

  toggleSubtasksFn() {
    this.toggleSubtasksFlag = !this.toggleSubtasksFlag;
  }

}



