import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ProjectsServiceProvider } from '../../providers/projects-service/projects-service';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { LoginPage } from '../login/login';
import { CommonListModal } from '../models/common-list-modal';
import { ProjectDetailsPage } from '../project-details/project-details';

@IonicPage()
@Component({
  selector: 'page-projects',
  templateUrl: 'projects.html',
})
export class ProjectsPage {

  public projectsList: CommonListModal[] = [];
  public projectsSearchList: CommonListModal[] = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public projectsServiceProvider: ProjectsServiceProvider,
    public authService: AuthenticationServiceProvider) {

    // Show Loader
    this.authService.showLoaderFn('');
    // API Call
    this.projectsServiceProvider.getAll().subscribe(data => {

      if(data["statusCode"] === 0) {
        this.projectsList = JSON.parse(data['response']);
        this.initializeItems();

        // Hide Loader
        this.authService.hideLoaderFn();
      } else {
        this.authService.handleResponseStatusFn(data);
        if(data["statusCode"] === 2) {
          this.navCtrl.setRoot(LoginPage);
        }
      }
      
    },
    err => {
      // Hide Loader and Show Message
      this.authService.handleErrorFn(err);
      if(err.status === 401) {
        this.navCtrl.setRoot(LoginPage);
      }
    });
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad ProjectsPage');
  }

  initializeItems() {
    this.projectsSearchList = [];
    this.projectsList.forEach(element => {
      this.projectsSearchList.push(element);
    });
  }

  getItems(ev: any) {
    // Reset items back to all of the items
    this.initializeItems();

    // set val to the value of the searchbar
    let val = ev.target.value;

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.projectsSearchList = this.projectsList.filter((item) => {
        return (item.name.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
  }

  viewProjectDetails(selectedProject) {
    this.navCtrl.push(ProjectDetailsPage, {
      selectedProject: selectedProject
    });
  }

}



