import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ContactsServiceProvider } from '../../providers/contacts-service/contacts-service';
import { Contact } from '../models/contact';
import { ContactFullDetailsPage } from '../contact-full-details/contact-full-details';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { LoginPage } from '../login/login';
import { ContactUIForm } from '../models/contact-ui-form';

/**
 * Generated class for the ContactsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-contacts',
  templateUrl: 'contacts.html',
})
export class ContactsPage {

  public contactsList: ContactUIForm[] = [];
  public contactsSearchList: ContactUIForm[] = [];

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public contactsServiceProvider: ContactsServiceProvider,
    public authService: AuthenticationServiceProvider) {
      
  }

  ionViewDidLoad() {

    // Show Loader
    this.authService.showLoaderFn('');
    // API Call
    this.contactsServiceProvider.getAll().subscribe(data => {

      if(data["statusCode"] === 0) {
        this.contactsList = JSON.parse(data['response']);;
        this.initializeItems();

        // Hide Loader
        this.authService.hideLoaderFn();
      } else {
        this.authService.handleResponseStatusFn(data);
        if(data["statusCode"] === 2) {
          this.navCtrl.setRoot(LoginPage);
        }
      }
      
    },
    err => {
      // Hide Loader and Show Message
      this.authService.handleErrorFn(err);
      if(err.status === 401) {
        this.navCtrl.setRoot(LoginPage);
      }
    });
  }

  initializeItems() {
    this.contactsSearchList = [];
    this.contactsList.forEach(element => {
      this.contactsSearchList.push(element);
    });
  }

  getItems(ev: any) {
    // Reset items back to all of the items
    this.initializeItems();

    // set val to the value of the searchbar
    let val = ev.target.value;

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.contactsSearchList = this.contactsList.filter((item) => {
        return (item.contactName.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
  }

  // openContactFullDetails(item) {
  //   this.navCtrl.push(ContactFullDetailsPage, {
  //     item: item
  //   });
  // }

  viewContactDetails(selectedContact) {
    this.navCtrl.push(ContactFullDetailsPage, {
      selectedContact: selectedContact
    });
  }
}



