import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CommonListModal } from '../models/common-list-modal';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { CompaniesServiceProvider } from '../../providers/companies-service/companies-service';
import { LoginPage } from '../login/login';
import { Company } from '../models/company';
import { UserDetailsMinimal } from '../models/user-details-minimal';
import { Organization } from '../models/organization';
import { Address } from '../models/address';

import * as moment from 'moment/moment';

@IonicPage()
@Component({
  selector: 'page-company-details',
  templateUrl: 'company-details.html',
})
export class CompanyDetailsPage {
  selectedCompany: Company;
  companyDetails: Company = new Company();

  organizationString: string = "";
  addressString: string = "";
  createdByString: string = "";
  updatedByString: string = "";

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public authService: AuthenticationServiceProvider,
    public companiesServiceProvider: CompaniesServiceProvider) {
    this.selectedCompany = navParams.get('selectedCompany');
    this.getCompanyDetailsByData(this.selectedCompany.companyId);
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad CompanyDetailsPage');
  }

  getCompanyDetailsByData(dataId) {
    // Show Loader
    this.authService.showLoaderFn('');
    // API Call
    this.companiesServiceProvider.getCompanyDetailsByData(dataId).subscribe(data => {

      if (data["statusCode"] === 0) {
        // Hide Loader
        this.authService.hideLoaderFn();
        this.companyDetails = JSON.parse(data['response']) as Company;

        this.createdByString = '';
        if(this.companyDetails.userDetailsByCreatedBy && this.companyDetails.userDetailsByCreatedBy.fullName) {
          this.createdByString += this.companyDetails.userDetailsByCreatedBy.fullName + ' on ';
          if(this.companyDetails.createTimestamp) {
            this.createdByString +=  moment(this.companyDetails.createTimestamp).format("DD MMM YYYY HH:mm");
          } 
        } else {
          this.createdByString = '----';
        }

        this.updatedByString = '';
        if(this.companyDetails.userDetailsByUpdatedBy && this.companyDetails.userDetailsByUpdatedBy.fullName) {
          this.updatedByString += this.companyDetails.userDetailsByUpdatedBy.fullName + ' on ';
          if(this.companyDetails.updateTimestamp) {
            this.updatedByString +=  moment(this.companyDetails.updateTimestamp).format("DD MMM YYYY HH:mm");
          } 
        } else {
          this.updatedByString = '----';
        }

        if(this.companyDetails.organization && this.companyDetails.organization.organizationName) {
          this.organizationString = this.companyDetails.organization.organizationName;
        } else {
          this.organizationString = '----';
        }  

        this.addressString = "";
        if(this.companyDetails.address) {

          if(this.companyDetails.address.addressLine1) {
            this.addressString +=  this.companyDetails.address.addressLine1;
          }

          if(this.companyDetails.address.addressLine2) {
            this.addressString +=  ", " + this.companyDetails.address.addressLine2;
          }

          if(this.companyDetails.address.state && this.companyDetails.address.state.name) {
            this.addressString +=  ", " + this.companyDetails.address.state.name;
          }

          if(this.companyDetails.address.country.name) {
            this.addressString +=  ", " + this.companyDetails.address.country.name;
          }

          if(this.companyDetails.address.pincode) {
            this.addressString +=  "- " + this.companyDetails.address.pincode;
          }
        }

      } else {
        this.authService.handleResponseStatusFn(data);
        if (data["statusCode"] === 2) {
          this.navCtrl.setRoot(LoginPage);
        }
      }
    }, err => {
      // Hide Loader and Show Message
      this.authService.handleErrorFn(err);
      if (err.status === 401) {
        this.navCtrl.setRoot(LoginPage);
      }
    });
  }

}



